export function deleteCanvas(id) {
    const canvas_old = document.getElementById(id);
    if (canvas_old) {
        canvas_old.remove();
    }
}
export function createCanvas(id, parent, width, height) {
    // Creating canvas
    const canvas = document.createElement("CANVAS");
    canvas.id = id;
    canvas.width = width;
    canvas.height = height;
    // Appending canvas
    parent.appendChild(canvas);
    return canvas;
}
